.header {
  --header-txt: 0, 0, 0;
  --header-orange: 255, 71, 29;
}
.header.dark-mode {
  --header-txt: 255, 255, 255;
}
.header__links {
  --default-width: 63.9rem;
  display: grid;
  justify-items: end;
  border-radius: 50px;
  transition: all 400ms ease;
  -webkit-transition: all 400ms ease;
  -moz-transition: all 400ms ease;
  -ms-transition: all 400ms ease;
  -o-transition: all 400ms ease;
}
.header__links-inner {
  grid-column: 1/2;
  grid-row: 1/2;
  padding: 0 35px;
  display: flex;
  column-gap: 6.4rem;
  justify-content: end;
  align-items: center;
  position: relative;
  z-index: 2;
  overflow: hidden;
  flex-wrap: nowrap;
  transition-delay: 400ms;
  transition: all 400ms ease;
  -webkit-transition: all 400ms ease;
  -moz-transition: all 400ms ease;
  -ms-transition: all 400ms ease;
  -o-transition: all 400ms ease;
}
.header__links-inner a {
  font-family: "Phudu", serif;
  font-weight: 700;
  color: #fff;
}
.header__links-toggle {
  grid-column: 1/2;
  grid-row: 1/2;
  width: 5rem;
  height: 5rem;
  position: relative;
  z-index: 1;
  pointer-events: none;
  transform-origin: right;
}
.header__links-toggle-icon {
  position: absolute;
  height: 2px;
  background-color: #fff;
  transform-origin: right;
  width: 100%;
  right: 1.3rem;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  transition: transform 400ms ease;
  -webkit-transition: transform 400ms ease;
  -moz-transition: transform 400ms ease;
  -ms-transition: transform 400ms ease;
  -o-transition: transform 400ms ease;
}
.header__links-toggle-icon-1 {
  width: 2.4rem;
  top: 1.7rem;
}
.header__links-toggle-icon-2 {
  width: 2.4rem;
  top: calc(2.4rem + 0.5px);
}
.header__links-toggle-icon-3 {
  width: 1.2rem;
  top: 3.2rem;
}
.header__link {
  display: block;
  color: rgba(var(--header-txt), 0.6);
  transition: all 400ms ease;
  -webkit-transition: all 400ms ease;
  -moz-transition: all 400ms ease;
  -ms-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  white-space: nowrap;
  transition-delay: calc(200ms + 40ms * var(--link-delay-index));
}
.header__link:nth-child(1) {
  --link-delay-index: 5;
}
.header__link:nth-child(2) {
  --link-delay-index: 4;
}
.header__link:nth-child(3) {
  --link-delay-index: 3;
}
.header__link:nth-child(4) {
  --link-delay-index: 2;
}
.header__link:nth-child(5) {
  --link-delay-index: 1;
}
.header__link:hover, .header__link.active {
  color: rgba(var(--header-txt), 1);
}
.header__toggle, .header__nav {
  display: none;
}
.header.on-scroll .header__links {
  background-color: #f6f6f6;
}
.header.on-scroll .header__links-inner {
  padding: 0 10px;
  max-width: 5rem;
  column-gap: 6.4rem;
  transition-delay: 0ms;
}
.header.on-scroll .header__links-toggle-icon {
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  transform-origin: left;
  opacity: 1;
  transition-delay: calc(100ms + 40ms * var(--link-delay-index));
}
.header.on-scroll .header__links-toggle-icon:nth-child(1) {
  --link-delay-index: 5;
}
.header.on-scroll .header__links-toggle-icon:nth-child(2) {
  --link-delay-index: 4;
}
.header.on-scroll .header__links-toggle-icon:nth-child(3) {
  --link-delay-index: 3;
}
.header.on-scroll .header__links-toggle-icon:nth-child(4) {
  --link-delay-index: 2;
}
.header.on-scroll .header__links-toggle-icon:nth-child(5) {
  --link-delay-index: 1;
}
.header.on-scroll .header__links .header__link {
  opacity: 0;
  transform: translateX(2rem);
  transition-delay: calc(100ms + 40ms * var(--link-delay-index));
}
.header.on-scroll .header__links:hover {
  width: auto;
  overflow: visible;
}
.header.on-scroll .header__links:hover .header__links-inner {
  padding: 0 3.2rem;
